import { DataService } from './../shared/data.service';
import { IVehicle } from './../model/vehicle';
import { Injectable } from '@angular/core';
import { Resolve, ActivatedRouteSnapshot, RouterStateSnapshot, Router } from '@angular/router';

import { Observable } from 'rxjs/Observable';
import 'rxjs/add/observable/of';
import 'rxjs/add/operator/catch';
import 'rxjs/add/operator/map';

import { VehicleService } from './vehicle.service';

@Injectable()
export class VehicleDetailsResolver implements Resolve<IVehicle> {

    constructor(
        private vehicleService: VehicleService,
        private dataService: DataService
    ) { }

    resolve(): Observable<IVehicle> {

        console.log('>>>>>> search parameteres', this.dataService.vehicleSearchParameters);

        return this.vehicleService.getVehicles()
            .map(vehicles => {
                console.log('resolver :', vehicles);
                return vehicles;
            })
            .catch(error => {
                console.log(`Retrieval error: ${error}`);
                return Observable.of(null);
            });
    }
}



