import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { MdButtonModule, MdCheckboxModule, MdToolbarModule, MdProgressBarModule } from '@angular/material';

@NgModule({
  imports: [
    CommonModule,
    MdButtonModule,
    MdCheckboxModule,
    MdToolbarModule,
    MdProgressBarModule
  ],
  exports: [
    MdButtonModule, 
    MdCheckboxModule,
    MdToolbarModule,
    MdProgressBarModule
  ],
  declarations: []
})
export class MaterialModule { }



