import { DataService } from './../../shared/data.service';
import { IVehicle } from './../../model/vehicle';
import {MdButtonModule} from '@angular/material';
import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';

@Component({
  selector: 'app-landing-search',
  templateUrl: './landing-search.component.html',
  styleUrls: ['./landing-search.component.css']
})
export class LandingSearchComponent implements OnInit {

  errorMessage: string;
  vehicles: IVehicle[];

  constructor(private router: Router, private dataService: DataService) { }

  ngOnInit() {
  }

  search() {

    this.dataService.vehicleSearchParameters = 'testparameters';
    this.router.navigate(['/book/stepone']);
  }
}



