import { MaterialModule } from './../material/material.module';
import { SharedModule } from './../shared/shared.module';
import { VehicleService } from './vehicle.service';
import { VehicleDetailsResolver } from './vehicle-details-resolver.service';
import { PaymentDetailsComponent } from './payment-details/payment-details.component';
import { CustomerDetailsComponent } from './customer-details/customer-details.component';
import { RentalOptionsComponent } from './rental-options/rental-options.component';
import { BookingComponent } from './booking/booking.component';
import { VehicleDetailsComponent } from './vehicle-details/vehicle-details.component';
import { RouterModule } from '@angular/router';
import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';

@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    MaterialModule,
    RouterModule.forChild([
      {
        path: 'book',
        component: BookingComponent,
        children: [
          { path: '',  redirectTo: 'stepone', pathMatch: 'full' },
          { path: 'stepone',
            component: VehicleDetailsComponent,
            resolve: { vehicleDetails: VehicleDetailsResolver },
          },
          { path: 'steptwo', component: RentalOptionsComponent},
          { path: 'stepthree', component: CustomerDetailsComponent},
          { path: 'stepfour', component: PaymentDetailsComponent},
        ]
      }
    ])
  ],
  declarations: [
    BookingComponent,
    VehicleDetailsComponent,
    BookingComponent,
    RentalOptionsComponent,
    CustomerDetailsComponent,
    PaymentDetailsComponent,
  ],
  providers: [
    VehicleService,
    VehicleDetailsResolver
  ]
})
export class BookingModule { }



